<template>
  <div class="search-panel">
    <el-row class="m-header-searchbar">
      <el-col :span="3" class="left">
        <img src="//s0.meituan.net/bs/fe-web-meituan/e5eeaef/img/logo.png" alt="美团">
      </el-col>
      <el-col :span="15" class="center">
        <div class="wrapper">
          <el-input v-model="searchWord" placeholder="write by 汤小灿，仅供学习参考" @focus="focus" @blur="blur" @input="inputText"></el-input>
          <el-button type="primary" icon="el-icon-search"></el-button>
          <dl class="hotPlace" v-if="isHotPlace">
            <dt>热门搜索</dt>
              <dd v-for="(item,index) in hotPlaceList" :key="index">
                <router-link :to="{name:'goods',params:{name:item}}">{{item}}</router-link>
            </dd>
          </dl>
          <dl class="searchList" v-if="isSearch">
            <dd v-for="(item,index) in isSearchList" :key="index">
                <!-- <router-link :to="{name:'goods',params:{name:item}}">{{item}}</router-link> -->
                 <router-link :to="{name: 'goods', params: {name: item}}">{{item}}</router-link>
            </dd>
          </dl>
        </div>
          <p class="suggest">
            <router-link v-for="(suggest,index) in suggestList" :key="index" to="/">{{suggest}}</router-link>
          </p>
      </el-col>
    </el-row>
  </div>
</template>
<script>
export default {
  data() {
    return {
      searchWord: "",
      isHotPlace: false,
      isSearch: false,
      hotPlaceList: ["火锅", "四川火锅", "火锅底料", "麻辣火锅"],
      isSearchList: ["火锅", "重庆火锅", "火锅底料", "麻辣火锅"],
      suggestList: [
        "北京故宫博物院",
        "北京欢乐谷",
        "尚隐·泉都市生活馆",
        "故宫珍宝馆"
      ]
    };
  },
  methods: {
    focus() {
      this.isHotPlace = true;
      if (this.searchWord) {
        this.isSearch = true;
      }
    },
    blur() {
      setTimeout(() => {
        this.isHotPlace = false;
        this.isSearch = false;
      }, 200);
    },
    inputText() {
      this.isSearch = true;
      this.isHotPlace = false;
      if (!this.searchWord) {
        this.isSearch = false;
      }
    }
  }
};
</script>
<style lang="scss">
@import "@/assets/css/public/header/index.scss";
@import "@/assets/css/public/header/search.scss";
</style>


