import Vue from 'vue'
import Router from 'vue-router'
import Default from '@/layout/Default'
import Blank from '@/layout/Blank'
import Index from '@/page/index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Default',
      component: Default,
      redirect: '/index',
      children: [
        {
          path: '/index',
          name: 'index',
          component: Index
        }]
    },
    {
      path: '/blank',
      name: 'Blank',
      component: Blank
    }
  ]
})



